*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.is-hidden {
  display: none;
}

.mobile-menu {
  width: 250px;
  height: 100%;
  z-index: 999;
  background-color: var(--main-color);
  flex-direction: column;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 1200px) {
  .mobile-menu {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .mobile-menu {
    width: 254px;
  }
}

.mobile-menu.is-open {
  filter: drop-shadow(-8px 0 20px rgba(0, 0, 0, .25));
  transform: translateX(0);
}

.mobile-menu__svg {
  cursor: pointer;
  top: 0;
  right: 0;
}

.mobile-men__burger-icon {
  cursor: pointer;
  fill: #fff;
  transition: all .25s;
}

.mobile-men__burger-icon:hover, .mobile-men__burger-icon:focus {
  fill: #9e9e9e;
  transition: all .3s cubic-bezier(.39, .575, .565, 1);
}

@media screen and (min-width: 768px) {
  .mobile-men__burger-icon {
    display: none;
  }
}

.another-svg {
  fill: #fff;
  cursor: pointer;
  margin-top: 20px;
  margin-right: 32px;
  transition: all .25s;
  position: absolute;
  top: 0;
  right: 0;
}

.another-svg:hover, .another-svg:focus {
  fill: #9e9e9e;
  transition: all .3s cubic-bezier(.39, .575, .565, 1);
}

.mobile-menu__button-container {
  justify-content: center;
  display: flex;
}

.mobile-menu__button {
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
}

.mobile-menu__join-button {
  background-color: var(--white-color);
