  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  transition: all .25s;
}

.mobile-menu__join-button:hover, .mobile-menu__join-button:focus {
  background-color: #c2c2c2;
  transition: all .3s cubic-bezier(.39, .575, .565, 1);
}

