.modal-login {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    z-index: 20;

    display: block;
    text-align: center;

    max-width: 300px;
    width: 100%;
    padding: 40px 20px;

    color: var(--white-color);
    background-color: #898989;
    border-radius: 4px;

    @media screen and (min-width: 768px) and (max-width:1199px) {
        & {
            padding: 40px 30px;
            max-width: 350px;
        }
    }

    @media screen and (min-width: 1100px) {
        & {
            max-width: 400px;
        }
    }

    &-close {
        position: absolute;
        top: 8px;
        right: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        padding: 0;

        // fill: var(--cl-txt-main);
        // background-color: var(--bgc-main);
        // border: none;

        // transition: fill var(--anim);
        // cursor: pointer;

        // &:hover,
        // &:focus {
        //     fill: var(--bgc-accent);
        // }
    }

    &-title {
        margin-bottom: 12px;

        text-align: center;
        font-weight: 700;
        letter-spacing: 0.03em;
        text-transform: uppercase;
    }
}

.form-user {
    text-align: center;
}

.form-group {
    display: block;
    margin-bottom: 15px;
    text-align: left;
}

.form-label {
    display: inline-block;
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    letter-spacing: 0.01em;
}

.form__wrapper {
    position: relative;
    display: block;
}

.form-input-auth {
    display: flex;
    width: 100%;
    height: 40px;
    padding: 5px 5px 5px 30px;

    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    cursor: pointer;

    .form-group:focus-within & {
        outline: 0;
        transition: all .3s cubic-bezier(.39, .575, .565, 1);
    }
}

.form-icon {
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translateY(-50%);
    fill: inherit;

    transition: fill var(--anim);

    .form-group:focus-within & {
        fill: var(--bgc-accent);
    }
}

.btn-submit {
    width: 200px;
    padding: 10px 55px;
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.06em;
    border: none;
    color: var(--fon-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    background-color: #0000;
    border-radius: 4px;
    transition: 250ms ease;

    cursor: pointer;

    &:hover,
    &:focus {
        transition: all .3s cubic-bezier(.39, .575, .565, 1);
        background-color: #c2c2c2;
    }

    &#btnSignup {
        margin-bottom: 10px;
    }
}

.btn__logout {
    width: 200px;
    letter-spacing: 0.06em;
    color: var(--fon-color);
    transition: background-color var(--anim), box-shadow var(--anim);
    background-color: #0000;
    border: 1px solid #fff;
    border-radius: 4px;
    padding: 10px 55px;
    font-size: 16px;
    line-height: 1.88;
    box-shadow: 0 4px 4px #00000026;
    cursor: pointer;

    &:hover,
    &:focus {
        background-color: var(--bgc-accent);
        border: 1px solid var(--cl-txt-accent);
    }
}

.success-message-form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.succes-title {
    font-size: 26px;
    text-align: center;
}

.succes-exit {
    font-size: 26px;
    text-align: center;
    margin-bottom: 10px;
}

.errorlabel {
    margin-top: 20px;
    color: var(--cl-txt-red);
}

.backdrop-notification {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 16;

    width: 100%;
    height: 100%;

    background: rgba(0, 0, 0, 0.2);

    transition: opacity var(--anim), visibility var(--anim),
        transform var(--anim);
}

.notification {
    z-index: 20;
    text-align: center;
    max-width: 300px;
    width: 100%;
    color: var(--cl-txt-main);
    background-color: var(--bgc-main);
    transition: transform var(--anim);
    border-radius: 4px;
    padding: 40px 20px;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);

    @media screen and (min-width: 768px) and (max-width:1199px) {
        padding: 40px 30px;
        max-width: 350px;
    }

    @media screen and (min-width:1200px) {
        max-width: 400px;
        top: 20%;
        left: 50%;
        transform: translate(-50%, -20%) scale(1);
    }
}